const Counters = () => {

    return (
        <section className="site-section-small section-counters light-gray-bg text-center">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-sm-3 col-xs-6 counter-line">
                        <h4 className="small-title">代码量</h4>
                        <p className="counter" data-to="1520" data-speed="2000">400<span style={{fontSize:'16px'}}>W+</span></p>
                    </div>

                    <div className="col-sm-3 col-xs-6 counter-line">
                        <h4 className="small-title">项目数</h4>
                        <p className="counter" data-to="426" data-speed="2000">30<span style={{fontSize:'16px'}}>+</span></p>
                    </div>

                    <div className="col-sm-3 col-xs-6 counter-line">
                        <h4 className="small-title">组件数</h4>
                        <p className="counter" data-to="512" data-speed="2000">1k<span style={{fontSize:'16px'}}>+</span></p>

                    </div>

                    <div className="col-sm-3 col-xs-6 counter-line">
                        <h4 className="small-title">发表字数</h4>
                        <p className="counter" data-to="423" data-speed="2000">12<span style={{fontSize:'16px'}}>W+</span></p>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Counters;