import { BrowserRouter, Routes } from 'react-router-dom';
import CRoutes from './routes';

export default () => (
    <BrowserRouter>
        <Routes>
            {/* <Route path="/" element={<App />} /> */}
            {CRoutes()}
            {/* <Route path="*" element={<NotFound />} /> */}
        </Routes>
    </BrowserRouter>
);
