import './index.less';
import CardImg1 from '../../assets/img/cards/1.png';
import CardImg2 from '../../assets/img/cards/2.png';
import CardImg3 from '../../assets/img/cards/3.png';

const DesignCard = () => {
    return (
        <div className="card-container">
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src={CardImg1} />
                        <h3>愿景</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>成为最值得信任、最有影响力的前端团队</p>
                        {/* <a href="#">Read More</a> */}
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src={CardImg2} />
                        <h3>使命</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>
                            坚持客户体验第一
                            <br />
                            为业务创造更多可能性
                        </p>
                        {/* <a href="#">Read More</a> */}
                    </div>
                </div>
            </div>
            <div className="card">
                <div className="face face1">
                    <div className="content">
                        <img src={CardImg3} />
                        <h3>文化</h3>
                    </div>
                </div>
                <div className="face face2">
                    <div className="content">
                        <p>
                            勇于承担、深入业务
                            <br />
                            群策群力、简单开放
                        </p>
                        {/* <a href="#">Read More</a> */}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default DesignCard;
